nav #navigation { float: right; margin: 0; padding: 0; }

nav #navigation li { display: inline; padding: 0; margin: 0; }

nav #navigation a:link, nav #navigation a:visited { color: #000; background: #b2b580; padding: 10px 20px; }

nav #some { float: left; margin: 0; padding: 0; }

nav #some li { display: inline; padding: 0; margin: 0; }

nav #some a:link, nav #some a:visited { text-decoration: none; }

nav #some a:link i, nav #some a:visited i { font-size: 30px; }

body { margin: 0; }

body main { background-image: url("../../img/backgrounds/bg1.jpg"); min-height: 100vh; width: 100%; background-position: center; background-repeat: no-repeat; background-size: cover; }
